// TypewriterEffect Component
const TypewriterEffect = ({ text }) => {
  const [currentTextIndex, setCurrentTextIndex] = React.useState(0);
  const [currentText, setCurrentText] = React.useState('');
  const [isDeleting, setIsDeleting] = React.useState(false);

  useEffect(() => {
    const timeout = setTimeout(() => {
      if (!isDeleting) {
        if (currentText.length < text[currentTextIndex].length) {
          setCurrentText(text[currentTextIndex].slice(0, currentText.length + 1));
        } else {
          setIsDeleting(true);
          setTimeout(() => {
            setIsDeleting(false);
            setCurrentTextIndex((currentTextIndex + 1) % text.length);
            setCurrentText('');
          }, 1000);
        }
      }
    }, 100);

    return () => clearTimeout(timeout);
  }, [currentText, currentTextIndex, isDeleting, text]);

  return <div>{currentText}<span className="animate-blink">_</span></div>;
};

